import React from 'react'
import Cards from '../components/Cards/Cards';
import Navbar from '../components/Navbar/Navbar';
import Footer from '../components/Footer/Footer';
import GraphFinal from '../components/FlotChart/FinalChart';

const Home = () => {
  return (
    <div className='Home' id='Home'>
        <Navbar/>
        <div className='row'>
        <div className='mainSection' id='mainSection'>  
            <Cards Source={"source1"} Energy={"energy1"}/>
            <Cards Source={"source2"} Energy={"energy2"}/>
            <GraphFinal Source={"source1"} id={"chart1"}/>
            <GraphFinal Source={"source2"} id={"chart2"}/>
        </div>
        <Footer/>
        </div>
    </div>
  )
}

export default Home