import React, { useEffect, useState } from "react";
import Plotly from "plotly.js-dist";

const GraphFinal = ({Source, id}) => {
  const [timeData, setTimeData] = useState([]); // x-axis data time line
  const [powerData, setPowerData] = useState([[], [], []]); // y-axis power values
  const chartID = id;
  const source = (Source === "source1" ? "Source 1" : "Source 2");
  var power1, power2, power3; 

  // display 15 datapoints at a time, and update the graph every 1s
  useEffect(() => {
    const interval = setInterval(() => {
      const storedValue = localStorage.getItem("mqttMessage");
      if (storedValue != null) {
        const jsonData = JSON.parse(storedValue).data;

        for(let i = 0; i < (jsonData[Source]).length; i++) {
          const phaseId = jsonData[Source][i].PID;
          switch(phaseId) {
            case "1":
              power1 = jsonData[Source][i].P;
              break;

            case "2":
              power2 = jsonData[Source][i].P;
              break;

            case "3":
              power3 = jsonData[Source][i].P;
              break;

            default:
              console.log("Invalid PhaseID");
          }
        }
      }

      const now = new Date();
      const timeString = now.toLocaleTimeString();
      setTimeData((prevtimeData) => [...prevtimeData, timeString].slice(-30));
      setPowerData((prevPowerData) => [
        [...prevPowerData[0], power1].slice(-30), // phase 1 power
        [...prevPowerData[1], power2].slice(-30), // phase 2 power
        [...prevPowerData[2], power3].slice(-30), // phase 3 power
      ]);
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  // plotting 3 data in a single graph as 3 flot charts for phases 1, 2 and 3
  useEffect(() => {
    Plotly.newPlot(
      chartID,
      [
        {
          x: timeData,
          y: powerData[0],
          type: "scatter",
          fill: "tozeroy",
          fillcolor: "rgba(28, 122, 176, 0.6)",
          mode: "lines",
          name: "Phase 1",
          marker: { color: "#459ed1", line: { color: "#1c7ab0" } },
        },
        {
          x: timeData,
          y: powerData[1],
          type: "scatter",
          fill: "tozeroy",
          fillcolor: "rgba(28, 122, 176, 0.4)",
          mode: "lines",
          name: "Phase 2",
          marker: { color: "#459ed1", line: { color: "#1c7ab0" } },
        },
        {
          x: timeData,
          y: powerData[2],
          type: "scatter",
          fill: "tozeroy",
          fillcolor: "rgba(28, 122, 176, 0.2)",
          mode: "lines",
          name: "Phase 3",
          marker: { color: "#459ed1", line: { color: "#1c7ab0" } },
        },
      ],
      // plot layout settings
      {
        title: "Power Representation: " + source,
        xaxis: {
          title: "Time",
          linecolor: "gray",
          gridcolor: "lightgray",
          standoff: 50,
          tickmode: "auto",
          nticks: 6
        },
        yaxis: {
          title: "Power (kW)",
          linecolor: "gray",
          color: "gray",
          gridcolor: "lightgray",
        },
        config: {
          displayModeBar: false, // hide the modebar on hover
          displaylogo: false,
        },
        paper_bgcolor: "rgba(0,0,0,0)",
        plot_bgcolor: "rgba(0,0,0,0)",
        autosize: true,
        bordercolor: "black",
        borderwidth: 2,
      }
    );
  }, [timeData, powerData]);

  // graph is drawn inside this div component
  return (
    <div className="graph">
      <div id={id} />
    </div>
  );
};

export default GraphFinal;
