import React from "react";
import "./Navbar.css";

const Navbar = () => {
  return (
    // Navbar fixed
    <nav className="navbar navbar-expand-lg navbar-dark bg-primary">
      <p className="navbar-brand">
        <img
          className="nav-logo"
          src="havellslogo.png"
          alt=""
          style={{ height: "40px", weight: "40px" }}
        />
      </p>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      {/* collapse-expand icon */}
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav mx-auto">
          <li className="nav-item active">
            <h4 className="nav-link title">Energy Auditor IP Dashboard</h4>
          </li>
        </ul>

        {/* Device Name   */}
        <div className="dropdown my-2 my-lg-0" style={{ marginRight: "1%" }}>
          <button className="btn btn-secondary dropdown-toggle bg-transparent border-0 deviceID" type="button" id="dropdownMenuButton" data-bs-toggle="dropdown" aria-expanded="false">
          981DA67BC82981343ADCBF6987F008D1EADBFC
          </button>
          <ul className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            <li><a className="dropdown-item" href="#">684DA67BC82981343ADCBF6987F008D1EADBFC</a></li>
            <li><a className="dropdown-item" href="#">781DA67BC82981343ADCBF6987F008D1EADBFC</a></li>
            <li><a className="dropdown-item" href="#">587DA67BC82981343ADCBF6987F008D1EADBFC</a></li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;