import React, { useEffect, useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlugCircleBolt,
  faPowerOff,
  faBolt,
} from "@fortawesome/free-solid-svg-icons";

const Cards = ({Source, Energy}) => {

  const [energy, setEnergy] = useState(0);
  const [powerP1, setPowerP1] = useState(0);
  const [powerP2, setPowerP2] = useState(0);
  const [powerP3, setPowerP3] = useState(0);
  const [currentP1, setCurrentP1] = useState(0);
  const [currentP2, setCurrentP2] = useState(0);
  const [currentP3, setCurrentP3] = useState(0);
  const [voltageP1, setVoltageP1] = useState(0);
  const [voltageP2, setVoltageP2] = useState(0);
  const [voltageP3, setVoltageP3] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      const storedValue = localStorage.getItem("mqttMessage");

      if (storedValue != null) {
        const jsonData = JSON.parse(storedValue).data;
        setEnergy(jsonData[Energy]);

        for (let i = 0; i < (jsonData[Source]).length; i++) {
          const phaseId = jsonData[Source][i].PID;
          switch (phaseId) {
            case "1":
              setPowerP1(jsonData[Source][i].P);
              setCurrentP1(jsonData[Source][i].I);
              setVoltageP1(jsonData[Source][i].V);
              break;

            case "2":
              setPowerP2(jsonData[Source][i].P);
              setCurrentP2(jsonData[Source][i].I);
              setVoltageP2(jsonData[Source][1].V);
              break;

            case "3":
              setPowerP3(jsonData[Source][i].P);
              setCurrentP3(jsonData[Source][i].I);
              setVoltageP3(jsonData[Source][i].V);
              break;

            default:
              console.log("Invalid PhaseID");
          }
        }
      }
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="flashCards row" id="parameterCards">
   
      <div className="card" style={{"width": "32vh"}} id='sourceControl'>
          <div className="card-body">
            <h5 className="card-title">{Source === "source1" ? "Source 1" : "Source 2"} </h5>
            <br></br>
            <h5 className="card-title">Energy : {energy} units</h5>
          </div>
      </div>

      {/* Phase Parameters- Power, Current and Voltage values for each phase are displayed in separate cards */}
      {/* Phase 1 parameters */}
      <div className="card" id="Phase1" style={{ width: "32vh" }}>
        <div className="card-body">
          <h5 className="card-title">Phase 1</h5>
          <table>
            <tbody>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPowerOff} />
                </td>
                <td className="parameter" id="Power1">
                  {powerP1} kW
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPlugCircleBolt} />
                </td>
                <td className="parameter" id="Current1">
                  {currentP1} A
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faBolt} />
                </td>
                <td className="parameter" id="Voltage1">
                  {voltageP1} V
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      {/* Phase 2 parameters */}
      <div className="card" id="Phase2" style={{ width: "32vh" }}>
        <div className="card-body">
          <h5 className="card-title">Phase 2</h5>
          <table>
            <tbody>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPowerOff} />
                </td>
                <td className="parameter" id="Power2">
                  {powerP2} kW
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPlugCircleBolt} />
                </td>
                <td className="parameter" id="Current2">
                  {currentP2} A
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faBolt} />
                </td>
                <td className="parameter" id="Voltage2">
                  {voltageP2} V
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      {/* Phase 3 parameters */}
      <div className="card" id="Phase3" style={{ width: "32vh" }}>
        <div className="card-body">
          <h5 className="card-title">Phase 3</h5>
          <table>
            <tbody>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPowerOff} />
                </td>
                <td className="parameter" id="Power3">
                  {powerP3} kW
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faPlugCircleBolt} />
                </td>
                <td className="parameter" id="Current3">
                  {currentP3} A
                </td>
              </tr>
              <tr>
                <td>
                  <FontAwesomeIcon icon={faBolt} />
                </td>
                <td className="parameter" id="Voltage3">
                  {voltageP3} V
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default Cards;
