const config = require("../config/config").energyAuditorv1;
const Paho = require("paho-mqtt");
const mqtt = require('mqtt');

const host = config.hostUrl;
const port = config.port;
const client = new Paho.Client(host, port, "");

function mqttConnection() {


  const clientId = 'mqttjs_' + Math.random().toString(16).substr(2, 8)
    const host = 'wss://broker.emqx.io:8084/mqtt'
    const options = {
      keepalive: 60,
      clientId: clientId,
      protocolId: 'MQTT',
      protocolVersion: 4,
      clean: true,
      reconnectPeriod: 1000,
      connectTimeout: 30 * 1000,
      will: {
        topic: 'WillMsg',
        payload: 'Connection Closed abnormally..!',
        qos: 0,
        retain: false
      },
    }
    console.log('Connecting mqtt client')
    const client = mqtt.connect(host, options)
    client.on('connect', () =>{
        console.log("connected");
        client.subscribe(config.subscribeTopic);
    });

client.on('message', (topic, message, packet) => {
  console.log(`Received Message: ${message.toString()} On topic: ${topic}`)
  localStorage.setItem("mqttMessage", message.toString());
})

    // client.on('message', () => {
    //   console.log("Message received:", message.toString());
    // localStorage.setItem("mqttMessage", message.payloadString);
    // })
    client.on('error', (err) => {
      console.log('Connection error: ', err)
      client.end()
    })
    client.on('reconnect', () => {
      console.log('Reconnecting...')
    })
  // console.log("Connecting to MQTT Broker.....");

  // client.onConnectionLost = onConnectionLost;

  // client.onMessageArrived = onMessageArrived;

  // client.connect({
  //   onSuccess: onConnect,
  //   userName: config.username,
  //   password: config.password,
  // });

  // function onConnect() {
  //   console.log("Connected!");
  //   client.subscribe(config.subscribeTopic);
  // }

  // function onMessageArrived(message) {
  //   console.log("Message received:", message.payloadString);
  //   localStorage.setItem("mqttMessage", message.payloadString);
  // }

  // function onConnectionLost(responseObject) {
  //   if (responseObject.errorCode !== 0) {
  //     console.log("Connection lost:", responseObject.errorMessage);
  //   }
  // }
}

function publishData() {
  let packet = {
    id: "90BCDA070",
    epochTime: Date.now(),
    pos: parseInt(localStorage.getItem("state")),
  };
  client.publish(config.publishTopic, JSON.stringify(packet));
  console.log("sent packet: " + JSON.stringify(packet));
}

module.exports = { mqttConnection, publishData };
